import logo from './logo.svg';
import './App.css';
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


import Chat from './Components/Chat'

function App() {
  return (
    <Router>
      <div className="App">
        <div  style={{margin:"0em 0"}}>
          <Switch>
            <Route exact path="/" component={Chat}/>
          </Switch>  
        </div>
        
      </div>
    </Router>
  );
}

export default App;
